.footer {
    background: #140d00;
    height: 26rem;



}

.footer .top {
    display: flex;
    padding: 27px 47px;
    justify-content: space-between;
    align-items: center;
}

.footer .top img {
    width: 17rem;
}

.footer .top .left a {
    margin-top: -1rem;
    border: 1px solid;
    padding: 11px;
    background-color: #000000;
    color: #ff9a00;
    text-decoration: none;
    width: 9rem;
    height: 1.5rem;
    font-size: 15px;
    font-weight: 700;
    transition: 0.2s;
    justify-content: center;
    display: flex;
    align-items: center;
}

.footer .top a:hover {
    background-color: #623b00;
    border-color: #4e3000;
    color: wheat;
}

.footer .top .left {
    display: flex;
    flex-direction: column;
    align-items: center;
}


.footer h3 {
    font-size: 27px;
    color: #ff9400;
}

.social .icons {
    font-size: 23px;
    display: flex;
    justify-content: space-evenly;
}
.social i{
    color: white;
}
.footer .right{
    color: white;
    display: flex;
    flex-direction: column;
}
.footer .right p{
    margin-top: 11px;
}

.footer .right p i{
    margin-right: 11px;
}
.copyright {
    background: black;
    height: 5rem;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
